            <p>
              <span v-if="settings.contact?.contact_email">Email: {{ settings.contact?.contact_email }}<br></span>
              <span v-if="settings.contact?.contact_phone">Telepon: {{ settings.contact?.contact_phone }}</span>
            </p>
