</template>

<script>
import ChatWidget from '@/components/ChatWidget.vue';

export default {
  name: 'UserDashboard',
  components: {
    ChatWidget
  },
  data() {
    return {
      settings: {}
    }
  },
  methods: {
    async fetchSettings() {
      try {
        const response = await this.$axios.get('/api/v2/settings');
        this.settings = response.data.data;
      } catch (error) {
        // Silent fail - use defaults
      }
    }
  },
  mounted() {
    this.fetchSettings();
  }
}
</script>

